.cart-dropdown{
    position: absolute;
    width: 240px;
   height: 340px;
   display:flex;
   flex-direction: column;
   padding:20px;
   border:1px solid black;
   background-color:white;
   top: 90px;right:40px;
   z-index: 5;


   .empty-message{
       font-size:18px;
       margin: 50px auto;
   }

   .cart-items {
       height:240px;
       display:flex;
       flex-direction: column;
       overflow-y: scroll;
   }

   button{
       margin-top: auto;
       padding: 10px;
    background-color: black;
    color: white;
    font-weight: 500;
    cursor: pointer;
   }

   button:hover{
       background-color: white;
       color: black;
       transition:0.5s;
   }
}