.collection-item {
    width: 22%;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
    margin-bottom: 30px;




    .custom-button{
     width:80%;
     opacity: 0.7;
     position: absolute;
      top: 255px;
      display: none;



      &:hover{
        background-color:white;
        color:black;
        border: 1px solid black;
        transition: 0.5s;
     
      }

    }
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }
  

    &:hover{
      .image{
        opacity: 0.8;
      }

      .custom-button{
        opacity: 0.85;
        display:flex;
     
      }
    }

    .collection-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
  
      .name {
        width: 90%;
        margin-bottom: 15px;
      }
  
      .price {
        width: 10%;
      }
    }
  }
  