.checkout-page{
    width: 55%;
    margin: 50px auto 0 ;
    min-height:90vh;
    display: flex;
    flex-direction: column;
    align-items: center;


    .checkout-header{
        width: 100%;
        padding: 10px 0;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid darkgrey;
    }


    .header-block{
        text-transform: capitalize;
        width: 23%;


        &:last-child{
            width: 8%;

        }
       
    }
}

.total{
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
    font-weight: bold;
}